import api from '../../services/api';
import { Creators } from '../ducks/prediction';
import { call, put } from 'redux-saga/effects';
import { actions as toastrActions } from 'react-redux-toastr';


export function* postPrediction({ filter }) {
  try {
    yield put(Creators.predictionInit());
    yield put(Creators.predictionRequest());

    // Abaixo, fazer chamada para descobrir e pegar as variveis utilizadas na gerao do modelo, de acordo com o phenomenon escolhido pelo usurio e passado no filter. Algo para se basear  o "getModelCopy". P.S.: No achei essas variveis salvas no banco, ento s devem estar em arquivo. No seria importante salvar tais variveis no banco de dados? P.S.: Pegar as variveis seguindo o modelo de ModelCopy.py | DONE

    // Aps pegar as variveis, fazer chamada para montar e executar a query que d um SELECT nas colunas correspondentes s variveis acima, passando as prprias variveis que foram pegas na chamada acima, e os outros atributos do filter que forem passados (cursos, disciplinas, turmas e perodo) vo estar no WHERE da query.

    // Aps a execuo da query acima, passar o retorno desse SELECT (todos os atributos e dados respectivos a cada aluno da base de dados (payload.data)) como payload para fazer a predio.
    const modelEndpoint = filter.phenomenon;
    const response = yield call(api.post, `predict/${modelEndpoint}`, filter);

    // yield put(toastrActions.add({
    //   type: 'success',
    //   title: 'Sucesso',
    //   message: `Anlise finalizada com sucesso!`
    // }));

    yield put(Creators.predictionSuccess(response.data));
  } catch (err) {
    yield put(Creators.predictionError({ err }));
    yield put(toastrActions.add({
      type: 'error',
      title: 'Erro',
      message: 'Falha ao gerar anlise'
    }));
  }
}